
@font-face {
	font-family:'noto';
	font-style:normal;
	font-weight:200;
	src:local('NotoSansLight'), local('NotoSansLight'),
		url(./resource/font/notokr/NotoSansKR-Light.eot);
	src:url(./resource/font/notokr/NotoSansKR-Light.eot?#iefix) format('embedded-opentype'),
		url(./resource/font/notokr/NotoSansKR-Light.woff2) format('woff2'),
		url(./resource/font/notokr/NotoSansKR-Light.woff) format('woff');;
}

